.Footer_view{
    /* border: 1px solid black; */
    width: 100%;
    height: 150px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

#socialMedia_view {
    /* border: 1px solid black; */
    width: 60px;
    height: 100%;
}

#socialMedia_view > .social_item {
    /* border: 1px solid black; */
    width: 100%;
    height: 50px;
    text-align: center;
    font-size: 33px;
    line-height: 50px;
    color: #c97100;
}

#pageNumber_view {
    /* border: 1px solid black; */
    min-width: 130px;
    height: 100%;
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
}

.number_page {
    /* border: 1px solid black; */
    width: 110px;
    height: 70%;
    color: #c97100;
    font-size: 110px;
    text-align: center;
}

.container_scroll {
    /* border: 1px solid black; */
    width: 60px;
    height: 100%;
}

.show-scroll {
    display: block;
}

.hide-scroll {
    display: none;
}

.scroll_btn {
    /* border: 1px solid black; */
    width: 80px;
    height: 60px;
    text-align: center;
    line-height: 59px;
    font-weight: bold;
    color: #c97100;
    transform: rotate(90deg);
    position: relative;
    right: 10px;
    top: 10px;
}

.scroll_line {
    /* border: 1px solid black; */
    width: 67px;
    height: 60px;
    transform: rotate(90deg);
    position: relative;
    right: 4.6px;
    top: 25px;

    display: flex;
    justify-content: center;
    align-items: center;
}

.scroll_line > .line {
    border: 1px solid #c97100;
    width: 90%;
}
