.Menu_S {
    /* border: 1px solid black; */
    background-color: #c97100;
    width: 45px;
    height: 45px;
    border-radius: 50px;
    transition: all 0.5s;
    position: absolute;
    top: -70px;
    display: flex;
    justify-content: space-around;
    align-items: center;
    overflow: hidden;
    z-index: 1;
}

.itemLink_S{

    text-decoration: none;
    color: #f5f5f5;
}



/***** SHOW MENU *****/

.first-show {
    /* border: 1px solid black; */
    width: 45px;
    height: 45px;
    animation-name: show_j;
    animation-duration: 0.5s;
    animation-fill-mode: forwards;
}

@keyframes show_j {
    0%{top: -70px;}
    100%{top: 33px;}  
}

.second-show {
    /* border: 1px solid black; */
    width: 45px;
    height: 45px;
    top: 33px;
    animation-name: show_d;
    animation-duration: 0.5s;
    animation-fill-mode: forwards;
}

@keyframes show_d {
    0%{width: 45px;}
    100%{width: 280px;}  
}

.third-show{
    /* border: 1px solid black; */
    width: 280px;
    height: 45px;
    top: 33px;
    justify-content: space-around;
}

/***** HIDE MENU ******/

.first-hide {
    /* border: 1px solid black; */
    width: 280px;
    height: 45px;
    top: 33px;
    animation-name: show_a;
    animation-duration: 0.4s;
    animation-fill-mode: forwards;
}

@keyframes show_a {
    0%{width:280px;}
    100%{width:45px;}  
}

.second-hide{
    /* border: 1px solid black; */
    width: 45px;
    height: 45px;
    animation-name: show_b;
    animation-duration: 0.5s;
    animation-fill-mode: forwards;
}

@keyframes show_b {
    0%{top: 33px;}
    100%{top:-70px ;}  
}

/************************** item*****/

.menuItem_S {
    /* border: 1px solid black; */
    color: #ffffff;
    width: 120px;
    height: 70%;
    text-align: center;
    line-height: 33px;
    font-weight: bold;
    display: none;
    opacity: 0%;
    transition: all 0.5s;
    
}

.yes-item {
    display: block;
}

.show-item {
    display: block;
    opacity: 100%;
}

.hide-item {
    display: block;
    opacity: 0%;
}

.no-item {
    display: none;
}

/************logo*****************/

.itemLogo {
    /* border: 1px solid black; */
    color: #ffffff;
    text-align: center;
    width: 70px;
    height: 70%;
    font-size: 40px;
    font-weight: bolder;
    line-height: 32px;
    opacity: 100%;
    transition: opacity 0.5s;
}

/* .yes-logo {
    display: block;
} */

.show-logo {
    opacity: 100%;
}

.hide-logo {
    opacity: 0%;
}

.no-logo {
    display: none;
}


