.Nav{
    /* border: 1px solid black; */
    width: 100%;
    height: 70px;
    display: flex;
    justify-content: space-between;
    transition: all 0.5s;
}

.Logo {
    /* border: 1px solid black; */
    width: 70px;
    height: 100%;
    background-image: url("./logo.png");
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center center;
    transition: opacity 0.5s;
}

.show-logo {
    opacity: 100%;
}

.hide-logo {
    opacity: 0%;
}