.Menu {
    /* border: 1px solid black; */
    width: 250px;
    height: 100%;
    display: flex;
    justify-content: space-around;
    align-items: center;
    transition: opacity 0.5s;
}

.hide-menu {
    opacity: 0%;
}

.show-menu {
    opacity: 100%;
}

.menuItem_L , .menu_Small_btn {
    /* border: 1px solid black;  */
    color: #c97100;
    width: 120px;
    height: 70%;
    text-align: center;
    line-height: 45px;
}

.menu_Small_btn {
    width: 80px;
    font-size: 20px;
    /* border: 1px solid black; */
    display: none;
}

@media (max-width:425px) {

    .Menu {
        justify-content: flex-end;
        width: auto;
    }
    .menuItem_L {
        display: none;
    }
    .menu_Small_btn{
        display: block;
    }
}